@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
.App{
    min-height: 100vh;
    background-color: rgb(2, 1, 0);
    font-family: 'Roboto', sans-serif;
}
.c{
  display: flex;
  justify-content: center;
  align-items: center;
}
.chest{
  max-width: 100%;
  width: 400px;
}
.status{
  position: absolute;
  top: 10%;
  left: 50%;
  font-size: 25px;
  transform: translateX(-50%);
  text-align: center;
  z-index: 1000;
}
.statusA{
  animation: status 0.35s ease-in-out;

}
.status_success{
  color: rgb(30, 214, 30);
}
.status_failure{
  color: red;
}
.anm_left{
  animation: animL 0.3s;
}
.anm_right{
  animation: animR 0.3s;
}
@keyframes status{
  from{
    transform: translate(-50%,-50px);
    opacity: 0;
  }
  to{
    transform: translate(-50%,0);
    opacity: 1;
  }
}
@keyframes animR{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}
@keyframes animL{
  0%{
    transform: scale(1) translateX(0);
  }
  50%{
    transform: scale(1.1) translateX(-30px);
  }
  100%{
    transform: scale(1) translateX(0);
  }
}
